import React from 'react'
import { Row, Col, Card, Statistic, Progress, List, Avatar, Tag, Space } from 'antd'
import {
  FileSearchOutlined,
  RobotOutlined,
  CheckCircleOutlined,
  ClockCircleOutlined,
  ExperimentOutlined,
  AlertOutlined
} from '@ant-design/icons'

const Dashboard: React.FC = () => {
  // 
  const stats = [
    {
      title: '',
      value: 1234,
      icon: <FileSearchOutlined style={{ color: '#1890ff' }} />,
      prefix: <FileSearchOutlined />
    },
    {
      title: '',
      value: 42,
      icon: <RobotOutlined style={{ color: '#52c41a' }} />,
      prefix: <RobotOutlined />
    },
    {
      title: '',
      value: 89,
      icon: <CheckCircleOutlined style={{ color: '#52c41a' }} />,
      prefix: <CheckCircleOutlined />
    },
    {
      title: '',
      value: 23,
      icon: <ClockCircleOutlined style={{ color: '#faad14' }} />,
      prefix: <ClockCircleOutlined />
    }
  ]

  const recentTasks = [
    {
      id: 1,
      title: ' #2024001',
      status: 'processing',
      priority: 'high',
      agent: '',
      time: '10'
    },
    {
      id: 2,
      title: ' #2024002',
      status: 'completed',
      priority: 'medium',
      agent: '',
      time: '25'
    },
    {
      id: 3,
      title: ' #2024003',
      status: 'pending',
      priority: 'low',
      agent: '',
      time: '1'
    }
  ]

  const systemStatus = [
    { name: '', status: 'normal', usage: 75 },
    { name: '', status: 'normal', usage: 60 },
    { name: '', status: 'warning', usage: 90 },
    { name: '', status: 'normal', usage: 45 }
  ]

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'processing': return 'processing'
      case 'completed': return 'success'
      case 'pending': return 'default'
      case 'error': return 'error'
      default: return 'default'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'processing': return ''
      case 'completed': return ''
      case 'pending': return ''
      case 'error': return ''
      default: return ''
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'red'
      case 'medium': return 'orange'
      case 'low': return 'green'
      default: return 'default'
    }
  }

  const getPriorityText = (priority: string) => {
    switch (priority) {
      case 'high': return ''
      case 'medium': return ''
      case 'low': return ''
      default: return ''
    }
  }

  return (
    <div>
      <h1></h1>
      
      {/*  */}
      <Row gutter={16} style={{ marginBottom: 24 }}>
        {stats.map((stat, index) => (
          <Col span={6} key={index}>
            <Card>
              <Statistic
                title={stat.title}
                value={stat.value}
                prefix={stat.prefix}
                valueStyle={{ color: stat.icon.props.style?.color }}
              />
            </Card>
          </Col>
        ))}
      </Row>

      <Row gutter={16}>
        {/*  */}
        <Col span={12}>
          <Card title="" extra={<a href="/cases"></a>}>
            <List
              dataSource={recentTasks}
              renderItem={item => (
                <List.Item>
                  <List.Item.Meta
                    avatar={<Avatar icon={<ExperimentOutlined />} />}
                    title={
                      <Space>
                        {item.title}
                        <Tag color={getPriorityColor(item.priority)}>
                          {getPriorityText(item.priority)}
                        </Tag>
                      </Space>
                    }
                    description={
                      <Space>
                        <Tag color={getStatusColor(item.status)}>
                          {getStatusText(item.status)}
                        </Tag>
                        <span>: {item.agent}</span>
                        <span>{item.time}</span>
                      </Space>
                    }
                  />
                </List.Item>
              )}
            />
          </Card>
        </Col>

        {/*  */}
        <Col span={12}>
          <Card title="">
            <List
              dataSource={systemStatus}
              renderItem={item => (
                <List.Item>
                  <List.Item.Meta
                    avatar={
                      item.status === 'normal' ? (
                        <CheckCircleOutlined style={{ color: '#52c41a' }} />
                      ) : (
                        <AlertOutlined style={{ color: '#faad14' }} />
                      )
                    }
                    title={item.name}
                    description={
                      <Progress
                        percent={item.usage}
                        status={item.usage > 80 ? 'exception' : 'normal'}
                        size="small"
                      />
                    }
                  />
                </List.Item>
              )}
            />
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Dashboard