import React, { useState, useEffect } from 'react'
import {
  Card,
  Table,
  Button,
  Space,
  Tag,
  Input,
  Select,
  Modal,
  Form,
  Row,
  Col,
  Typography,
  Tabs,
  List,
  
  Progress,
  Tooltip,
  Badge,
  
} from 'antd'
import {
  PlusOutlined,
  SearchOutlined,
  EyeOutlined,
  EditOutlined,
  
  DownloadOutlined,
  FileSearchOutlined,
  ExperimentOutlined,
  
  CheckCircleOutlined,
  ClockCircleOutlined,
  
} from '@ant-design/icons'
import type { ColumnsType } from 'antd/es/table'
// removed unused imports (redux, dayjs, caseSlice) to reduce noise

const { Title, Text } = Typography
const { TabPane } = Tabs
const { Search } = Input
// no RangePicker/TextArea used here

interface FailureCase {
  id: number
  caseNumber: string
  title: string
  description: string
  productName: string
  productModel: string
  failureDate: string
  failureLocation: string
  failureMode: string
  failureMechanism: string
  severityLevel: string
  status: 'PENDING' | 'PROCESSING' | 'COMPLETED' | 'CLOSED'
  creatorId: number
  assigneeId: number
  createTime: string
  updateTime: string
  creator?: {
    id: number
    realName: string
  }
  assignee?: {
    id: number
    realName: string
  }
}

// CaseStatistics not used in current implementation

interface CaseDetail {
  case: FailureCase | Case
  tags?: string[]
  attachments?: number
  tasks: Array<{
    id: string
    name: string
    status: string
    agent: string
    progress: number
    startTime: string
  }>
  results: Array<{
    type: string
    name: string
    url: string
    createdAt: string
  }>
}

const CaseManagement: React.FC = () => {
  const [cases] = useState<Case[]>([
    {
      id: '1',
      caseNumber: 'FA2024001',
      title: '',
      description: '',
      failureType: '',
      severityLevel: 'high',
      status: 'processing',
      assignedAgent: '',
      createdAt: '2024-01-15 09:30:00',
      updatedAt: '2024-01-15 10:45:00',
      progress: 65,
      tags: ['', '', ''],
      attachments: 5
    },
    {
      id: '2',
      caseNumber: 'FA2024002',
      title: '',
      description: '',
      failureType: '',
      severityLevel: 'medium',
      status: 'completed',
      assignedAgent: '',
      createdAt: '2024-01-14 14:20:00',
      updatedAt: '2024-01-14 16:30:00',
      progress: 100,
      tags: ['', ''],
      attachments: 8
    },
    {
      id: '3',
      caseNumber: 'FA2024003',
      title: '',
      description: '',
      failureType: '',
      severityLevel: 'critical',
      status: 'pending',
      createdAt: '2024-01-15 11:00:00',
      updatedAt: '2024-01-15 11:00:00',
      progress: 0,
      tags: ['', ''],
      attachments: 3
    }
  ])

  const [selectedCase, setSelectedCase] = useState<Case | null>(null)
  const [caseModalVisible, setCaseModalVisible] = useState(false)
  const [createModalVisible, setCreateModalVisible] = useState(false)
  const [form] = Form.useForm()

  const getSeverityColor = (level: string) => {
    switch (level) {
      case 'low': return 'green'
      case 'medium': return 'orange'
      case 'high': return 'red'
      case 'critical': return 'magenta'
      default: return 'default'
    }
  }

  const getSeverityText = (level: string) => {
    switch (level) {
      case 'low': return ''
      case 'medium': return ''
      case 'high': return ''
      case 'critical': return ''
      default: return ''
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'default'
      case 'processing': return 'processing'
      case 'completed': return 'success'
      case 'closed': return 'default'
      default: return 'default'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'pending': return ''
      case 'processing': return ''
      case 'completed': return ''
      case 'closed': return ''
      default: return ''
    }
  }

  const caseColumns: ColumnsType<Case> = [
    {
      title: '',
      dataIndex: 'caseNumber',
      key: 'caseNumber',
      render: (text: string) => <Text strong>{text}</Text>
    },
    {
      title: '',
      dataIndex: 'title',
      key: 'title',
      ellipsis: true
    },
    {
      title: '',
      dataIndex: 'failureType',
      key: 'failureType'
    },
    {
      title: '',
      dataIndex: 'severityLevel',
      key: 'severityLevel',
      render: (level: string) => (
        <Tag color={getSeverityColor(level)}>
          {getSeverityText(level)}
        </Tag>
      )
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => (
        <Badge status={getStatusColor(status) as any} text={getStatusText(status)} />
      )
    },
    {
      title: '',
      dataIndex: 'progress',
      key: 'progress',
      render: (progress: number) => (
        <Progress percent={progress} size="small" />
      )
    },
    {
      title: '',
      dataIndex: 'assignedAgent',
      key: 'assignedAgent',
      render: (agent?: string) => agent ? <Tag color="blue">{agent}</Tag> : '-'
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt'
    },
    {
      title: '',
      key: 'action',
      render: (_, record: Case) => (
        <Space>
          <Tooltip title="">
            <Button
              type="text"
              icon={<EyeOutlined />}
              onClick={() => {
                setSelectedCase(record)
                setCaseModalVisible(true)
              }}
            />
          </Tooltip>
          <Tooltip title="">
            <Button type="text" icon={<EditOutlined />} />
          </Tooltip>
          <Tooltip title="">
            <Button type="text" icon={<DownloadOutlined />} />
          </Tooltip>
        </Space>
      )
    }
  ]

  const mockCaseDetail: CaseDetail = {
    case: cases[0],
    tasks: [
      {
        id: '1',
        name: '',
        status: 'completed',
        agent: '',
        progress: 100,
        startTime: '2024-01-15 09:30:00'
      },
      {
        id: '2',
        name: '',
        status: 'processing',
        agent: '',
        progress: 65,
        startTime: '2024-01-15 10:00:00'
      },
      {
        id: '3',
        name: '',
        status: 'pending',
        agent: '',
        progress: 0,
        startTime: ''
      }
    ],
    results: [
      {
        type: 'image',
        name: '',
        url: '/images/processed_001.jpg',
        createdAt: '2024-01-15 09:45:00'
      },
      {
        type: 'report',
        name: '',
        url: '/reports/preliminary_001.pdf',
        createdAt: '2024-01-15 10:15:00'
      }
    ]
  }

  return (
    <div>
      <Title level={2}></Title>
      
      {/*  */}
      <Card style={{ marginBottom: 16 }}>
        <Row gutter={16}>
          <Col span={8}>
            <Search
              placeholder=""
              allowClear
              enterButton={<SearchOutlined />}
            />
          </Col>
          <Col span={4}>
            <Select placeholder="" style={{ width: '100%' }} allowClear>
              <Select.Option value="surface_defect"></Select.Option>
              <Select.Option value="welding_defect"></Select.Option>
              <Select.Option value="composition_abnormal"></Select.Option>
              <Select.Option value="structural_failure"></Select.Option>
            </Select>
          </Col>
          <Col span={4}>
            <Select placeholder="" style={{ width: '100%' }} allowClear>
              <Select.Option value="low"></Select.Option>
              <Select.Option value="medium"></Select.Option>
              <Select.Option value="high"></Select.Option>
              <Select.Option value="critical"></Select.Option>
            </Select>
          </Col>
          <Col span={4}>
            <Select placeholder="" style={{ width: '100%' }} allowClear>
              <Select.Option value="pending"></Select.Option>
              <Select.Option value="processing"></Select.Option>
              <Select.Option value="completed"></Select.Option>
              <Select.Option value="closed"></Select.Option>
            </Select>
          </Col>
          <Col span={4}>
            <Button type="primary" icon={<PlusOutlined />} onClick={() => setCreateModalVisible(true)}>
              
            </Button>
          </Col>
        </Row>
      </Card>

      {/*  */}
      <Card title="">
        <Table
          columns={caseColumns}
          dataSource={cases}
          rowKey="id"
          pagination={{ pageSize: 10 }}
        />
      </Card>

      {/*  */}
      <Modal
        title=""
        open={caseModalVisible}
        onCancel={() => setCaseModalVisible(false)}
        footer={[
          <Button key="close" onClick={() => setCaseModalVisible(false)}>
            
          </Button>
        ]}
        width={1000}
      >
        {selectedCase && (
          <Tabs defaultActiveKey="info">
            <TabPane tab="" key="info">
              <Row gutter={16}>
                <Col span={12}>
                  <p><strong>:</strong> {selectedCase.caseNumber}</p>
                  <p><strong>:</strong> {selectedCase.title}</p>
                  <p><strong>:</strong> {selectedCase.description}</p>
                  <p><strong>:</strong> {selectedCase.failureType}</p>
                </Col>
                <Col span={12}>
                  <p><strong>:</strong> 
                                    <Tag color={getSeverityColor(selectedCase.severityLevel || '')}>
                                      {getSeverityText(selectedCase.severityLevel || '')}
                                    </Tag>
                  </p>
                  <p><strong>:</strong> 
                    <Badge status={getStatusColor(selectedCase.status || '') as any} text={getStatusText(selectedCase.status || '')} />
                  </p>
                  <p><strong>:</strong> {selectedCase.assignedAgent || ''}</p>
                  <p><strong>:</strong> {selectedCase.createdAt}</p>
                </Col>
              </Row>
              <Row>
                <Col span={24}>
                  <p><strong>:</strong></p>
                  <Space wrap>
                    {selectedCase.tags.map(tag => (
                      <Tag key={tag} color="blue">{tag}</Tag>
                    ))}
                  </Space>
                </Col>
              </Row>
            </TabPane>

            <TabPane tab="" key="tasks">
              <List
                dataSource={mockCaseDetail.tasks}
                renderItem={task => (
                  <List.Item>
                    <List.Item.Meta
                      avatar={
                        task.status === 'completed' ? (
                          <CheckCircleOutlined style={{ color: '#52c41a' }} />
                        ) : task.status === 'processing' ? (
                          <ClockCircleOutlined style={{ color: '#1890ff' }} />
                        ) : (
                          <ClockCircleOutlined style={{ color: '#d9d9d9' }} />
                        )
                      }
                      title={task.name}
                      description={
                        <div>
                          <Text type="secondary">: {task.agent}</Text>
                          <br />
                          <Text type="secondary">: {task.startTime}</Text>
                          <Progress percent={task.progress} size="small" style={{ marginTop: 8 }} />
                        </div>
                      }
                    />
                  </List.Item>
                )}
              />
            </TabPane>

            <TabPane tab="" key="results">
              <List
                dataSource={mockCaseDetail.results}
                renderItem={result => (
                  <List.Item
                    actions={[
                      <Button key="download" type="text" icon={<DownloadOutlined />}>
                        
                      </Button>
                    ]}
                  >
                    <List.Item.Meta
                      avatar={
                        result.type === 'image' ? (
                          <FileSearchOutlined />
                        ) : (
                          <ExperimentOutlined />
                        )
                      }
                      title={result.name}
                      description={`: ${result.createdAt}`}
                    />
                  </List.Item>
                )}
              />
            </TabPane>
          </Tabs>
        )}
      </Modal>

      {/*  */}
      <Modal
        title=""
        open={createModalVisible}
        onCancel={() => setCreateModalVisible(false)}
        onOk={() => form.submit()}
        okText=""
        cancelText=""
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={(values) => {
            console.log(':', values)
            setCreateModalVisible(false)
            form.resetFields()
          }}
        >
          <Form.Item
            name="title"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            name="description"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input.TextArea rows={4} placeholder="" />
          </Form.Item>

          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name="failureType"
                label=""
                rules={[{ required: true, message: '' }]}
              >
                <Select placeholder="">
                  <Select.Option value="surface_defect"></Select.Option>
                  <Select.Option value="welding_defect"></Select.Option>
                  <Select.Option value="composition_abnormal"></Select.Option>
                  <Select.Option value="structural_failure"></Select.Option>
                </Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name="severityLevel"
                label=""
                rules={[{ required: true, message: '' }]}
              >
                <Select placeholder="">
                  <Select.Option value="low"></Select.Option>
                  <Select.Option value="medium"></Select.Option>
                  <Select.Option value="high"></Select.Option>
                  <Select.Option value="critical"></Select.Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    </div>
  )
}

export default CaseManagement