import React, { useState } from 'react'
import {
  Card,
  Row,
  Col,
  Table,
  Button,
  Space,
  Tag,
  Progress,
  Modal,
  Form,
  Input,
  Select,
  Avatar,
  List,
  Typography,
  Tabs,
  Badge,
  Tooltip
} from 'antd'
import {
  RobotOutlined,
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  PlayCircleOutlined,
  PauseCircleOutlined,
  SettingOutlined,
  ThunderboltOutlined,
  EyeOutlined,
  MessageOutlined
} from '@ant-design/icons'
import type { ColumnsType } from 'antd/es/table'

const { Title, Text } = Typography
const { TabPane } = Tabs
const { TextArea } = Input

interface Agent {
  id: string
  name: string
  type: string
  status: 'active' | 'inactive' | 'busy' | 'error'
  capabilities: string[]
  currentTask?: string
  performance: {
    cpu: number
    memory: number
    successRate: number
    avgResponseTime: number
  }
  createdAt: string
  lastActive: string
}

interface AgentTask {
  id: string
  agentId: string
  taskName: string
  status: 'pending' | 'running' | 'completed' | 'failed'
  progress: number
  startTime: string
  estimatedCompletion?: string
}

const MultiAgentCluster: React.FC = () => {
  const [agents] = useState<Agent[]>([
    {
      id: '1',
      name: '',
      type: 'vision',
      status: 'active',
      capabilities: ['', '', ''],
      currentTask: '',
      performance: {
        cpu: 65,
        memory: 78,
        successRate: 95.5,
        avgResponseTime: 2.3
      },
      createdAt: '2024-01-10 00:00:00',
      lastActive: '2024-01-15 10:45:00'
    },
    {
      id: '2',
      name: '',
      type: 'spectral',
      status: 'busy',
      capabilities: ['', '', ''],
      currentTask: '',
      performance: {
        cpu: 82,
        memory: 71,
        successRate: 92.1,
        avgResponseTime: 3.1
      },
      createdAt: '2024-01-11 00:00:00',
      lastActive: '2024-01-15 10:50:00'
    },
    {
      id: '3',
      name: '',
      type: 'data',
      status: 'inactive',
      capabilities: ['', '', ''],
      performance: {
        cpu: 15,
        memory: 25,
        successRate: 98.2,
        avgResponseTime: 1.8
      },
      createdAt: '2024-01-12 00:00:00',
      lastActive: '2024-01-14 16:30:00'
    }
  ])

  const [agentTasks] = useState<AgentTask[]>([
    {
      id: '1',
      agentId: '1',
      taskName: '',
      status: 'running',
      progress: 75,
      startTime: '2024-01-15 10:30:00',
      estimatedCompletion: '2024-01-15 11:00:00'
    },
    {
      id: '2',
      agentId: '2',
      taskName: '',
      status: 'running',
      progress: 60,
      startTime: '2024-01-15 10:15:00',
      estimatedCompletion: '2024-01-15 11:15:00'
    },
    {
      id: '3',
      agentId: '1',
      taskName: '',
      status: 'pending',
      progress: 0,
      startTime: '2024-01-15 11:00:00'
    }
  ])

  const [agentModalVisible, setAgentModalVisible] = useState(false)
  const [selectedAgent, setSelectedAgent] = useState<Agent | null>(null)
  const [form] = Form.useForm()

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'success'
      case 'inactive': return 'default'
      case 'busy': return 'processing'
      case 'error': return 'error'
      default: return 'default'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'active': return ''
      case 'inactive': return ''
      case 'busy': return ''
      case 'error': return ''
      default: return ''
    }
  }

  const getAgentTypeText = (type: string) => {
    switch (type) {
      case 'vision': return ''
      case 'spectral': return ''
      case 'data': return ''
      case 'llm': return ''
      case 'fusion': return ''
      default: return ''
    }
  }

  const agentColumns: ColumnsType<Agent> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (text: string, record: Agent) => (
        <Space>
          <Avatar icon={<RobotOutlined />} />
          <div>
            <div>{text}</div>
            <Text type="secondary" style={{ fontSize: 12 }}>
              {getAgentTypeText(record.type)}
            </Text>
          </div>
        </Space>
      )
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => (
        <Badge status={getStatusColor(status) as any} text={getStatusText(status)} />
      )
    },
    {
      title: '',
      dataIndex: 'capabilities',
      key: 'capabilities',
      render: (capabilities: string[]) => (
        <Space wrap>
          {capabilities.map(cap => (
            <Tag key={cap}>{cap}</Tag>
          ))}
        </Space>
      )
    },
    {
      title: '',
      dataIndex: 'currentTask',
      key: 'currentTask',
      render: (task?: string) => task ? <Text ellipsis>{task}</Text> : '-'
    },
    {
      title: '',
      key: 'performance',
      render: (_, record: Agent) => (
        <Space direction="vertical" size="small">
          <Progress percent={record.performance.cpu} size="small" showInfo={false} />
          <Text type="secondary" style={{ fontSize: 12 }}>
            : {record.performance.successRate}% | : {record.performance.avgResponseTime}s
          </Text>
        </Space>
      )
    },
    {
      title: '',
      key: 'action',
      render: (_, record: Agent) => (
        <Space>
          <Tooltip title="">
            <Button
              type="text"
              icon={<EyeOutlined />}
              onClick={() => {
                setSelectedAgent(record)
                setAgentModalVisible(true)
              }}
            />
          </Tooltip>
          <Tooltip title="">
            <Button type="text" icon={<SettingOutlined />} />
          </Tooltip>
          <Tooltip title="">
            <Button type="text" icon={<MessageOutlined />} />
          </Tooltip>
        </Space>
      )
    }
  ]

  const taskColumns: ColumnsType<AgentTask> = [
    {
      title: '',
      dataIndex: 'taskName',
      key: 'taskName'
    },
    {
      title: '',
      dataIndex: 'agentId',
      key: 'agentId',
      render: (agentId: string) => {
        const agent = agents.find(a => a.id === agentId)
        return agent ? agent.name : ''
      }
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => (
        <Tag color={status === 'running' ? 'processing' : status === 'completed' ? 'success' : status === 'failed' ? 'error' : 'default'}>
          {status === 'running' ? '' : status === 'completed' ? '' : status === 'failed' ? '' : ''}
        </Tag>
      )
    },
    {
      title: '',
      dataIndex: 'progress',
      key: 'progress',
      render: (progress: number) => (
        <Progress percent={progress} size="small" />
      )
    },
    {
      title: '',
      dataIndex: 'startTime',
      key: 'startTime'
    },
    {
      title: '',
      dataIndex: 'estimatedCompletion',
      key: 'estimatedCompletion',
      render: (time?: string) => time || '-'
    }
  ]

  return (
    <div>
      <Title level={2}></Title>
      
      <Tabs defaultActiveKey="agents">
        <TabPane tab="" key="agents">
          <Card
            title=""
            extra={
              <Button type="primary" icon={<PlusOutlined />}>
                
              </Button>
            }
          >
            <Table
              columns={agentColumns}
              dataSource={agents}
              rowKey="id"
              pagination={{ pageSize: 10 }}
            />
          </Card>
        </TabPane>

        <TabPane tab="" key="tasks">
          <Card title="">
            <Table
              columns={taskColumns}
              dataSource={agentTasks}
              rowKey="id"
              pagination={{ pageSize: 10 }}
            />
          </Card>
        </TabPane>

        <TabPane tab="" key="statistics">
          <Row gutter={16}>
            <Col span={6}>
              <Card>
                <div style={{ textAlign: 'center' }}>
                  <Badge count={agents.filter(a => a.status === 'active').length} showZero>
                    <RobotOutlined style={{ fontSize: 48, color: '#1890ff' }} />
                  </Badge>
                  <div style={{ marginTop: 16 }}>
                    <Title level={4}></Title>
                    <Text type="secondary">
                      {agents.filter(a => a.status === 'active').length} / {agents.length}
                    </Text>
                  </div>
                </div>
              </Card>
            </Col>
            <Col span={6}>
              <Card>
                <div style={{ textAlign: 'center' }}>
                  <ThunderboltOutlined style={{ fontSize: 48, color: '#52c41a' }} />
                  <div style={{ marginTop: 16 }}>
                    <Title level={4}></Title>
                    <Text type="secondary">
                      {agentTasks.filter(t => t.status === 'running').length} 
                    </Text>
                  </div>
                </div>
              </Card>
            </Col>
            <Col span={6}>
              <Card>
                <div style={{ textAlign: 'center' }}>
                  <div style={{ fontSize: 48, color: '#faad14' }}>
                    {Math.round(agents.reduce((acc, a) => acc + a.performance.successRate, 0) / agents.length)}%
                  </div>
                  <div style={{ marginTop: 16 }}>
                    <Title level={4}></Title>
                    <Text type="secondary"></Text>
                  </div>
                </div>
              </Card>
            </Col>
            <Col span={6}>
              <Card>
                <div style={{ textAlign: 'center' }}>
                  <div style={{ fontSize: 48, color: '#722ed1' }}>
                    {Math.round(agents.reduce((acc, a) => acc + a.performance.avgResponseTime, 0) / agents.length)}s
                  </div>
                  <div style={{ marginTop: 16 }}>
                    <Title level={4}></Title>
                    <Text type="secondary"></Text>
                  </div>
                </div>
              </Card>
            </Col>
          </Row>
        </TabPane>

        <TabPane tab="" key="communication">
          <Card title="">
            <List
              dataSource={[
                {
                  from: '',
                  to: '',
                  message: '',
                  time: '2024-01-15 10:45:23'
                },
                {
                  from: '',
                  to: '',
                  message: '',
                  time: '2024-01-15 10:42:15'
                },
                {
                  from: '',
                  to: '',
                  message: '',
                  time: '2024-01-15 10:40:00'
                }
              ]}
              renderItem={item => (
                <List.Item>
                  <List.Item.Meta
                    avatar={<MessageOutlined />}
                    title={
                      <Space>
                        <Text strong>{item.from}</Text>
                        <Text type="secondary"></Text>
                        <Text strong>{item.to}</Text>
                      </Space>
                    }
                    description={
                      <div>
                        <Text>{item.message}</Text>
                        <br />
                        <Text type="secondary" style={{ fontSize: 12 }}>
                          {item.time}
                        </Text>
                      </div>
                    }
                  />
                </List.Item>
              )}
            />
          </Card>
        </TabPane>
      </Tabs>

      {/*  */}
      <Modal
        title=""
        open={agentModalVisible}
        onCancel={() => setAgentModalVisible(false)}
        footer={[
          <Button key="close" onClick={() => setAgentModalVisible(false)}>
            
          </Button>
        ]}
        width={800}
      >
        {selectedAgent && (
          <div>
            <Row gutter={16}>
              <Col span={12}>
                <Card size="small" title="">
                  <p><strong>:</strong> {selectedAgent.name}</p>
                  <p><strong>:</strong> {getAgentTypeText(selectedAgent.type)}</p>
                  <p><strong>:</strong> 
                    <Badge 
                      status={getStatusColor(selectedAgent.status) as any} 
                      text={getStatusText(selectedAgent.status)} 
                    />
                  </p>
                  <p><strong>:</strong> {selectedAgent.createdAt}</p>
                  <p><strong>:</strong> {selectedAgent.lastActive}</p>
                </Card>
              </Col>
              <Col span={12}>
                <Card size="small" title="">
                  <p><strong>CPU:</strong> {selectedAgent.performance.cpu}%</p>
                  <p><strong>:</strong> {selectedAgent.performance.memory}%</p>
                  <p><strong>:</strong> {selectedAgent.performance.successRate}%</p>
                  <p><strong>:</strong> {selectedAgent.performance.avgResponseTime}s</p>
                </Card>
              </Col>
            </Row>
            <Card size="small" title="" style={{ marginTop: 16 }}>
              <Space wrap>
                {selectedAgent.capabilities.map(cap => (
                  <Tag key={cap} color="blue">{cap}</Tag>
                ))}
              </Space>
            </Card>
            {selectedAgent.currentTask && (
              <Card size="small" title="" style={{ marginTop: 16 }}>
                <Text>{selectedAgent.currentTask}</Text>
              </Card>
            )}
          </div>
        )}
      </Modal>
    </div>
  )
}

export default MultiAgentCluster