import React, { useState } from 'react'
import {
  Card,
  Row,
  Col,
  Upload,
  Button,
  Space,
  Tabs,
  Image,
  Typography,
  Progress,
  List,
  Tag,
  Modal,
  Form,
  Input,
  Select
} from 'antd'
import {
  InboxOutlined,
  UploadOutlined,
  EyeOutlined,
  DeleteOutlined,
  FileImageOutlined,
  FileTextOutlined,
  VideoCameraOutlined,
  SoundOutlined
} from '@ant-design/icons'
import type { UploadProps, UploadFile } from 'antd'

const { Dragger } = Upload
const { Title, Text } = Typography
const { TabPane } = Tabs
const { TextArea } = Input

interface UploadedFile {
  id: string
  name: string
  type: string
  size: number
  status: string
  url?: string
  preview?: string
}

interface AnalysisTask {
  id: string
  name: string
  type: string
  status: 'pending' | 'processing' | 'completed' | 'failed'
  progress: number
  files: string[]
  createdAt: string
}

const MultimodalWorkbench: React.FC = () => {
  const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([])
  const [analysisTasks, setAnalysisTasks] = useState<AnalysisTask[]>([])
  const [previewModalVisible, setPreviewModalVisible] = useState(false)
  const [previewFile, setPreviewFile] = useState<UploadedFile | null>(null)
  const [taskModalVisible, setTaskModalVisible] = useState(false)
  const [form] = Form.useForm()

  // 
  const mockTasks: AnalysisTask[] = [
    {
      id: '1',
      name: '',
      type: 'image_analysis',
      status: 'processing',
      progress: 75,
      files: ['chip_image_001.jpg', 'chip_image_002.jpg'],
      createdAt: '2024-01-15 10:30:00'
    },
    {
      id: '2',
      name: '',
      type: 'document_analysis',
      status: 'completed',
      progress: 100,
      files: ['failure_report.pdf'],
      createdAt: '2024-01-15 09:15:00'
    }
  ]

  const uploadProps: UploadProps = {
    name: 'file',
    multiple: true,
    accept: 'image/*,.pdf,.doc,.docx,.txt,.mp4,.avi,.mp3,.wav',
    customRequest: ({ file, onSuccess, onError }) => {
      // 
      setTimeout(() => {
        const newFile: UploadedFile = {
          id: Date.now().toString(),
          name: (file as File).name,
          type: (file as File).type,
          size: (file as File).size,
          status: 'done',
          url: URL.createObjectURL(file as any),
          preview: URL.createObjectURL(file as any)
        }
        setUploadedFiles(prev => [...prev, newFile])
        onSuccess?.(newFile)
      }, 1000)
    },
    onDrop(e) {
      console.log('Dropped files', e.dataTransfer.files)
    }
  }

  const handlePreview = (file: UploadedFile) => {
    setPreviewFile(file)
    setPreviewModalVisible(true)
  }

  const handleDelete = (fileId: string) => {
    setUploadedFiles(prev => prev.filter(f => f.id !== fileId))
  }

  const handleCreateTask = (values: any) => {
    const newTask: AnalysisTask = {
      id: Date.now().toString(),
      name: values.taskName,
      type: values.taskType,
      status: 'pending',
      progress: 0,
      files: values.selectedFiles,
      createdAt: new Date().toLocaleString()
    }
    setAnalysisTasks(prev => [...prev, newTask])
    setTaskModalVisible(false)
    form.resetFields()
  }

  const getFileIcon = (type: string) => {
    if (type.startsWith('image/')) return <FileImageOutlined />
    if (type.includes('pdf') || type.includes('document')) return <FileTextOutlined />
    if (type.startsWith('video/')) return <VideoCameraOutlined />
    if (type.startsWith('audio/')) return <SoundOutlined />
    return <FileTextOutlined />
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'default'
      case 'processing': return 'processing'
      case 'completed': return 'success'
      case 'failed': return 'error'
      default: return 'default'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'pending': return ''
      case 'processing': return ''
      case 'completed': return ''
      case 'failed': return ''
      default: return ''
    }
  }

  const renderPreviewContent = () => {
    if (!previewFile) return null

    if (previewFile.type.startsWith('image/')) {
      return (
        <Image
          src={previewFile.preview}
          alt={previewFile.name}
          style={{ maxWidth: '100%' }}
        />
      )
    }

    return (
      <div style={{ textAlign: 'center', padding: '40px' }}>
        {getFileIcon(previewFile.type)}
        <p>{previewFile.name}</p>
        <Text type="secondary">: {(previewFile.size / 1024).toFixed(2)} KB</Text>
      </div>
    )
  }

  return (
    <div>
      <Title level={2}></Title>
      
      <Row gutter={16}>
        {/*  */}
        <Col span={12}>
          <Card title="" style={{ marginBottom: 16 }}>
            <Dragger {...uploadProps}>
              <p className="ant-upload-drag-icon">
                <InboxOutlined />
              </p>
              <p className="ant-upload-text"></p>
              <p className="ant-upload-hint">
                
              </p>
            </Dragger>
          </Card>

          {/*  */}
          <Card title="">
            <List
              dataSource={uploadedFiles}
              renderItem={file => (
                <List.Item
                  actions={[
                    <Button
                      key="preview"
                      type="text"
                      icon={<EyeOutlined />}
                      onClick={() => handlePreview(file)}
                    />,
                    <Button
                      key="delete"
                      type="text"
                      danger
                      icon={<DeleteOutlined />}
                      onClick={() => handleDelete(file.id)}
                    />
                  ]}
                >
                  <List.Item.Meta
                    avatar={getFileIcon(file.type)}
                    title={file.name}
                    description={`${(file.size / 1024).toFixed(2)} KB`}
                  />
                </List.Item>
              )}
            />
          </Card>
        </Col>

        {/*  */}
        <Col span={12}>
          <Card
            title=""
            extra={
              <Button
                type="primary"
                icon={<UploadOutlined />}
                onClick={() => setTaskModalVisible(true)}
                disabled={uploadedFiles.length === 0}
              >
                
              </Button>
            }
          >
            <List
              dataSource={mockTasks}
              renderItem={task => (
                <List.Item>
                  <List.Item.Meta
                    title={
                      <Space>
                        {task.name}
                        <Tag color={getStatusColor(task.status)}>
                          {getStatusText(task.status)}
                        </Tag>
                      </Space>
                    }
                    description={
                      <div>
                        <Text type="secondary">: {task.createdAt}</Text>
                        {task.status === 'processing' && (
                          <Progress
                            percent={task.progress}
                            size="small"
                            style={{ marginTop: 8 }}
                          />
                        )}
                      </div>
                    }
                  />
                </List.Item>
              )}
            />
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Modal
        title=""
        open={previewModalVisible}
        onCancel={() => setPreviewModalVisible(false)}
        footer={null}
        width={800}
      >
        {renderPreviewContent()}
      </Modal>

      {/*  */}
      <Modal
        title=""
        open={taskModalVisible}
        onCancel={() => setTaskModalVisible(false)}
        onOk={() => form.submit()}
        okText=""
        cancelText=""
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleCreateTask}
        >
          <Form.Item
            name="taskName"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            name="taskType"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder="">
              <Select.Option value="image_analysis"></Select.Option>
              <Select.Option value="document_analysis"></Select.Option>
              <Select.Option value="video_analysis"></Select.Option>
              <Select.Option value="audio_analysis"></Select.Option>
              <Select.Option value="multimodal_fusion"></Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            name="selectedFiles"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Select
              mode="multiple"
              placeholder=""
              style={{ width: '100%' }}
            >
              {uploadedFiles.map(file => (
                <Select.Option key={file.id} value={file.id}>
                  {file.name}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item name="description" label="">
            <TextArea
              rows={4}
              placeholder=""
            />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  )
}

export default MultimodalWorkbench