import React, { useState } from 'react'
import {
  Card,
  Row,
  Col,
  Table,
  Button,
  Space,
  Tag,
  Progress,
  Modal,
  Form,
  Input,
  Select,
  Timeline,
  Typography,
  Tabs,
  List,
  Avatar
} from 'antd'
import {
  RobotOutlined,
  PlayCircleOutlined,
  PauseCircleOutlined,
  StopOutlined,
  ClockCircleOutlined,
  CheckCircleOutlined,
  ExclamationCircleOutlined,
  PlusOutlined,
  SettingOutlined
} from '@ant-design/icons'
import type { ColumnsType } from 'antd/es/table'

const { Title, Text } = Typography
const { TabPane } = Tabs
const { TextArea } = Input

interface Task {
  id: string
  name: string
  type: string
  status: 'pending' | 'running' | 'completed' | 'failed' | 'paused'
  priority: 'low' | 'medium' | 'high' | 'urgent'
  progress: number
  assignedAgent?: string
  createdAt: string
  estimatedTime?: number
  actualTime?: number
}

interface Workflow {
  id: string
  name: string
  description: string
  status: 'active' | 'inactive' | 'running'
  tasks: string[]
  createdAt: string
}

const IntelligentHub: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([
    {
      id: '1',
      name: '',
      type: 'image_analysis',
      status: 'running',
      priority: 'high',
      progress: 65,
      assignedAgent: '',
      createdAt: '2024-01-15 10:30:00',
      estimatedTime: 30,
      actualTime: 20
    },
    {
      id: '2',
      name: '',
      type: 'spectral_analysis',
      status: 'completed',
      priority: 'medium',
      progress: 100,
      assignedAgent: '',
      createdAt: '2024-01-15 09:15:00',
      estimatedTime: 45,
      actualTime: 42
    },
    {
      id: '3',
      name: '',
      type: 'pattern_recognition',
      status: 'pending',
      priority: 'urgent',
      progress: 0,
      createdAt: '2024-01-15 11:00:00',
      estimatedTime: 60
    }
  ])

  const [workflows] = useState<Workflow[]>([
    {
      id: '1',
      name: '',
      description: '',
      status: 'active',
      tasks: ['1', '2', '3'],
      createdAt: '2024-01-10 00:00:00'
    },
    {
      id: '2',
      name: '',
      description: '',
      status: 'inactive',
      tasks: ['1'],
      createdAt: '2024-01-12 00:00:00'
    }
  ])

  const [taskModalVisible, setTaskModalVisible] = useState(false)
  const [workflowModalVisible, setWorkflowModalVisible] = useState(false)
  const [form] = Form.useForm()

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'default'
      case 'running': return 'processing'
      case 'completed': return 'success'
      case 'failed': return 'error'
      case 'paused': return 'warning'
      default: return 'default'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'pending': return ''
      case 'running': return ''
      case 'completed': return ''
      case 'failed': return ''
      case 'paused': return ''
      default: return ''
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'low': return 'green'
      case 'medium': return 'orange'
      case 'high': return 'red'
      case 'urgent': return 'magenta'
      default: return 'default'
    }
  }

  const getPriorityText = (priority: string) => {
    switch (priority) {
      case 'low': return ''
      case 'medium': return ''
      case 'high': return ''
      case 'urgent': return ''
      default: return ''
    }
  }

  const handleTaskAction = (taskId: string, action: string) => {
    setTasks(prev => prev.map(task => {
      if (task.id === taskId) {
        switch (action) {
          case 'start':
            return { ...task, status: 'running' as const }
          case 'pause':
            return { ...task, status: 'paused' as const }
          case 'stop':
            return { ...task, status: 'pending' as const, progress: 0 }
          default:
            return task
        }
      }
      return task
    }))
  }

  const taskColumns: ColumnsType<Task> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
      render: (text: string, record: Task) => (
        <Space>
          <RobotOutlined />
          <div>
            <div>{text}</div>
            <Text type="secondary" style={{ fontSize: 12 }}>
              {record.type}
            </Text>
          </div>
        </Space>
      )
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => (
        <Tag color={getStatusColor(status)}>
          {getStatusText(status)}
        </Tag>
      )
    },
    {
      title: '',
      dataIndex: 'priority',
      key: 'priority',
      render: (priority: string) => (
        <Tag color={getPriorityColor(priority)}>
          {getPriorityText(priority)}
        </Tag>
      )
    },
    {
      title: '',
      dataIndex: 'progress',
      key: 'progress',
      render: (progress: number, record: Task) => (
        <Progress
          percent={progress}
          size="small"
          status={record.status === 'failed' ? 'exception' : 'normal'}
        />
      )
    },
    {
      title: '',
      dataIndex: 'assignedAgent',
      key: 'assignedAgent',
      render: (agent?: string) => (
        agent ? <Tag color="blue">{agent}</Tag> : '-'
      )
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt'
    },
    {
      title: '',
      key: 'action',
      render: (_, record: Task) => (
        <Space>
          {record.status === 'pending' && (
            <Button
              type="text"
              icon={<PlayCircleOutlined />}
              onClick={() => handleTaskAction(record.id, 'start')}
            >
              
            </Button>
          )}
          {record.status === 'running' && (
            <Button
              type="text"
              icon={<PauseCircleOutlined />}
              onClick={() => handleTaskAction(record.id, 'pause')}
            >
              
            </Button>
          )}
          {(record.status === 'running' || record.status === 'paused') && (
            <Button
              type="text"
              danger
              icon={<StopOutlined />}
              onClick={() => handleTaskAction(record.id, 'stop')}
            >
              
            </Button>
          )}
        </Space>
      )
    }
  ]

  return (
    <div>
      <Title level={2}></Title>
      
      <Tabs defaultActiveKey="tasks">
        <TabPane tab="" key="tasks">
          <Card
            title=""
            extra={
              <Button
                type="primary"
                icon={<PlusOutlined />}
                onClick={() => setTaskModalVisible(true)}
              >
                
              </Button>
            }
          >
            <Table
              columns={taskColumns}
              dataSource={tasks}
              rowKey="id"
              pagination={{ pageSize: 10 }}
            />
          </Card>
        </TabPane>

        <TabPane tab="" key="workflows">
          <Row gutter={16}>
            <Col span={12}>
              <Card
                title=""
                extra={
                  <Button
                    type="primary"
                    icon={<PlusOutlined />}
                    onClick={() => setWorkflowModalVisible(true)}
                  >
                    
                  </Button>
                }
              >
                <List
                  dataSource={workflows}
                  renderItem={workflow => (
                    <List.Item
                      actions={[
                        <Button key="edit" type="text" icon={<SettingOutlined />} />,
                        <Button key="run" type="primary" size="small">
                          
                        </Button>
                      ]}
                    >
                      <List.Item.Meta
                        avatar={<Avatar icon={<RobotOutlined />} />}
                        title={
                          <Space>
                            {workflow.name}
                            <Tag color={workflow.status === 'active' ? 'green' : 'default'}>
                              {workflow.status === 'active' ? '' : ''}
                            </Tag>
                          </Space>
                        }
                        description={workflow.description}
                      />
                    </List.Item>
                  )}
                />
              </Card>
            </Col>

            <Col span={12}>
              <Card title="">
                <Timeline>
                  <Timeline.Item color="green">
                    <Text>""</Text>
                    <br />
                    <Text type="secondary">2024-01-15 10:45:00</Text>
                  </Timeline.Item>
                  <Timeline.Item color="blue">
                    <Text>""</Text>
                    <br />
                    <Text type="secondary">2024-01-15 09:15:00</Text>
                  </Timeline.Item>
                  <Timeline.Item color="orange">
                    <Text>""</Text>
                    <br />
                    <Text type="secondary">2024-01-15 08:30:00</Text>
                  </Timeline.Item>
                </Timeline>
              </Card>
            </Col>
          </Row>
        </TabPane>

        <TabPane tab="" key="resources">
          <Row gutter={16}>
            <Col span={8}>
              <Card title="CPU">
                <Progress type="circle" percent={65} />
              </Card>
            </Col>
            <Col span={8}>
              <Card title="">
                <Progress type="circle" percent={78} />
              </Card>
            </Col>
            <Col span={8}>
              <Card title="GPU">
                <Progress type="circle" percent={45} />
              </Card>
            </Col>
          </Row>
        </TabPane>
      </Tabs>

      {/*  */}
      <Modal
        title=""
        open={taskModalVisible}
        onCancel={() => setTaskModalVisible(false)}
        onOk={() => form.submit()}
        okText=""
        cancelText=""
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={(values) => {
            const newTask: Task = {
              id: Date.now().toString(),
              name: values.name,
              type: values.type,
              status: 'pending',
              priority: values.priority,
              progress: 0,
              createdAt: new Date().toLocaleString(),
              estimatedTime: values.estimatedTime
            }
            setTasks(prev => [...prev, newTask])
            setTaskModalVisible(false)
            form.resetFields()
          }}
        >
          <Form.Item
            name="name"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            name="type"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder="">
              <Select.Option value="image_analysis"></Select.Option>
              <Select.Option value="spectral_analysis"></Select.Option>
              <Select.Option value="pattern_recognition"></Select.Option>
              <Select.Option value="data_fusion"></Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            name="priority"
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder="">
              <Select.Option value="low"></Select.Option>
              <Select.Option value="medium"></Select.Option>
              <Select.Option value="high"></Select.Option>
              <Select.Option value="urgent"></Select.Option>
            </Select>
          </Form.Item>

          <Form.Item name="estimatedTime" label="">
            <Input type="number" placeholder="" />
          </Form.Item>

          <Form.Item name="description" label="">
            <TextArea rows={4} placeholder="" />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  )
}

export default IntelligentHub